@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
/*
GOOGLE FONTS
*/

/* 
Variables
*/

:root {
  --color-white: #fff;
  --color-black-1: #000000;
  --color-black-2: #07070a;
  --font-primary: "Lato", sans-serif;
  --transition: all 0.3s linear;
  --spacing: 0.25rem;
  --radius: 1rem;
  --max-width: 500px;
  --shadow: 0 5px 15px rgba(255, 255, 255, 0.4);
}
/*
   GLOBAL STYLES, FASTER DEVELOPMENT, THIS IS REUSABLE CODE
    THIS GLOBAL STYLES ARE OVERRIDED IF NEEDED
*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Lato", sans-serif;
  font-family: var(--font-primary);
  background: #fff;
  background: var(--color-white);
  line-height: 1.5;
  font-size: 0.875rem;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
img {
  width: 100%;
  display: block;
}
h1,
h2,
h3,
h4 {
  letter-spacing: 0.25rem;
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 2.8rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.875rem;
}
p {
  margin-bottom: 1.25rem;
}
@media screen and (min-width: 800px) {
  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}
*:focus {
  -webkit-tap-highlight-color: transparent;
  outline: none;
  touch-action: manipulation;
}

#root {
  background-color: #000000;
  background-color: var(--color-black-1);
  height: 100vh;
  display: grid;
  place-items: center;
}

.calculator {
  height: 85vh;
  width: 90%;
  max-width: var(--max-width);
  margin: 0 auto;
}

.screen {
  height: 25vh;
  box-shadow: var(--shadow);
  color: var(--color-white);
  font-size: 2.5rem;
  text-align: end;
  padding: 2rem 1rem 1rem 1rem;
}
@media screen and (max-height: 400px) {
  .screen {
    padding: 1rem 0.5rem 0.5rem 0.5rem;
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 400px) {
  .small {
    font-size: 1.7rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
}

.keyboard {
  background-color: var(--color-black-1);
  height: 60vh;
  width: 90%;
  margin: 0 auto;
  box-shadow: var(--shadow);
  margin-top: 2px;
  border-radius: 0.5rem;
}

.btn {
  outline: none;
  border: none;
  display: inline-block;
  color: var(--color-white);
  background: var(--color-black-1);
  border: 1px solid #7dcfb6;
  margin-left: 10px;
  margin-top: 10px;
  border-radius: var(--radius);
  font-weight: bold;
  cursor: pointer;
  font-size: 1.7rem;
  transition: var(--transition);
}
.btn-large {
  color: #1d4e89;
  letter-spacing: 2px;
  text-transform: uppercase;
}
.btn-operators,
.btn-result {
  color: #1d4e89;
  background: #7dcfb6;
  border: none;
  font-size: 1.5rem;
}
@media screen and (max-height: 400px) {
  .btn {
    font-size: 1.2rem;
    margin-left: 7px;
    margin-top: 7px;
  }
  .btn-operators,
  .btn-result {
    font-size: 1rem;
  }
}

